* {
  box-sizing: border-box;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  overflow-x: hidden;
}

main, .container {
  width: 1300px;
  max-width: 95%;
  margin: 0 auto;
  padding: 0.3em 1em;
}

nav {
  background-color: #444444;
  height: 3rem;

  .container {
    height: 100%;
    padding-bottom: 0;
    
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 2em;
  }

  h1 {
    color: white;
  }

  ul {
    list-style: none;
    margin: 0;
    padding: 0;

    display: flex;
    align-self: flex-end;
    gap: 1em;

    font-size: 1.1rem;
  }

  .nav-link {
    display: block;

    background-color: transparent;
    border: none;
    color: white;
    text-decoration: none;

    padding-bottom: 0.1em;
    border-bottom: 5px solid transparent;
    transition-duration: 0.3s;

    &:hover {
      color: rgb(79, 198, 245);
    }

    &.active {
      color: rgb(79, 198, 245);
      border-bottom-color: rgb(79, 198, 245);
    }
  }
}

form {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 0.3em;

  border: 1px solid black;
  padding: 2em;

  width: 300px;
  max-width: 90%;

  label {
    margin-top: 0.5em;
    margin-left: 1em;

    font-weight: bold;
    font-size: 1.2rem;
  }

  button, input {
    width: 100%;
  }

  button {
    margin-top: 30px;
  }
}

input {
  border-radius: 8px;
  outline: none;
  border: none;
  border: 2px solid rgba(0, 0, 0, 0.5);

  padding: 0.5em 1em;

  &:focus {
    border-color: rgb(9, 149, 168);
  }
}

.btn {
  display: inline-flex;
  justify-content: center;
  align-items: center;

  padding: 0.3em 1em;

  cursor: pointer;

  background-color: white;
  border: 1px solid black;
  border-radius: 4px;
  color: black;

  font-size: 1rem;
  text-decoration: none;

  transition-duration: 0.3s;

  &:hover {
    background-color: #EEEEEE;
  }

  &.btn-primary {
    background-color: rgb(9, 149, 168);
    color: white;
    border-color: black;

    &:hover:not(:disabled) {
      background-color: rgb(15, 116, 129);
    }
  }

  &.btn-danger {
    background-color: rgb(170, 7, 7);
    color: white;
    border-color: black;

    &:hover:not(:disabled) {
      background-color: rgb(144, 9, 9);
    }
  }

  &:disabled {
    opacity: 0.6;
    cursor: not-allowed;
  }
}

dl {
  display: grid;
  grid-template-columns: auto 1fr;
  align-items: center;
  gap: 0.2em 1em;

  dt {
    font-weight: bold;
  }

  dd {
    margin: 0;
  }
}
